<script>
import Slick from "vue-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default {
  name: "Gallery",
  components: { Slick },

  data() {
    return {
      slickOptions: {
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 3,
        dots: true,
        speed: 400,
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
