<template>
  <div id="profile">
    <div>
      <h1
        class="uk-heading-line uk-text-center uk-h1 uk-padding-small title"
        uk-scrollspy="cls:uk-animation-slide-top-small"
      >
        <span>Our Profile</span>
      </h1>
      <div>
        <div class="uk-column-1-2@l uk-padding-large">
          <div
            uk-scrollspy="cls:uk-animation-slide-left-medium"
            class="uk-padding-large uk-padding-remove-horizontal uk-padding-remove-top"
          >
            <img
              src="https://raw.githubusercontent.com/aytidasaputra/pictures/master/nita.png"
              :alt="wanita.nama"
              class="uk-border-circle uk-width-medium@l uk-width-large uk-box-shadow-large uk-width-small"
            />
            <div class="uk-padding-small uk-padding-remove-bottom">
              <h2 class="uk-h2">{{ wanita.nama }}</h2>
              <p
                class="text-deskripsi uk-width-large@l uk-width-xxlarge uk-text-justify uk-margin-auto-right uk-margin-auto-left"
              >
                {{ wanita.deskripsi }}
              </p>
            </div>
          </div>
          <div
            uk-scrollspy="cls:uk-animation-slide-right-medium"
            class="uk-padding-large uk-padding-remove-horizontal uk-padding-remove-bottom@m uk-padding-remove-bottom@s uk-padding-remove-bottom@xs uk-padding-remove-top"
          >
            <img
              src="https://raw.githubusercontent.com/aytidasaputra/pictures/master/zaky.png"
              :alt="pria.nama"
              class="uk-border-circle uk-width-medium@l uk-width-large uk-box-shadow-large"
            />
            <div class="uk-padding-small uk-padding-remove-bottom">
              <h2 class="uk-h2">{{ pria.nama }}</h2>
              <p
                class="text-deskripsi uk-width-large@l uk-width-xxlarge uk-text-justify uk-margin-auto-right uk-margin-auto-left"
              >
                {{ pria.deskripsi }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Profile",

  data() {
    return {
      pria: {
        nama: "Nuruddin Abdul Kodir Azzaky, S.Kom",
        deskripsi: "Putra Pertama Bapak Maryanto dan Ibu Sumarni",
      },
      wanita: {
        nama: "Yunita Yuliawati, S.Kom",
        deskripsi: "Putri Pertama dari Bapak A. Shobari dan Ibu Pajarwati",
      },
    };
  },
};
</script>

<style lang="css" scoped>
#profile {
  padding-top: 1px;
  background-color: #fef8e9;
}
.text-deskripsi {
  color: #666666 !important;
}

@media screen and (max-width: 1024px) {
}
</style>



